#navService {
    position: fixed;
    z-index: 9999;
}

@media (min-width: 768px){
  #navService {
  
    display: flex;
    pointer-events: auto;
    width: 47px;
    top: 145px;
    right: 0;
    height: 165px;
    transition: transform .4s ease;
    transition: transform .4s ease,-webkit-transform .4s ease;
}

 #navService {

    transform: translateY(55px)!important;
}
#navService.down {

    transform: translateY(0)!important;
}

#navService .navHead, #navService .navInfo {
 
    transition: transform .3s cubic-bezier(.215,.61,.355,1);

}

#navService .navHead {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 22;
    width: 50px;
    height: 165px;
    background:#1273EB;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
 
    box-shadow: -5px 2px 20px 0 rgba(51,51,51,.2);
    color: #fff;
    font-size: 16px;
    line-height: 1.33;
    letter-spacing: 1px;
    text-align: center;
    padding: 20px 15px;
    position: relative;
    cursor: pointer;
}

#navService.no-hover.open .navHead, #navService.no-hover.open .navInfo, #navService:hover:not(.no-hover) .navHead, #navService:hover:not(.no-hover) .navInfo {
   
    transform: translateX(-210px);
}

#navService .navHead i {
    width: 50px;
    font-size: 14px;
    bottom: 20px;
    position: absolute;
    left: 0;
  
    animation: tip 1s linear 1.5s 3;
}

#navService.no-hover.open .navHead, #navService.no-hover.open .navInfo, #navService:hover:not(.no-hover) .navHead, #navService:hover:not(.no-hover) .navInfo {

    transform: translateX(-210px);

}
#navService .navInfo a .navInfo__icon:before {
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    border: solid 3px #f9f9f9;
    border-radius: 50%;
 
    box-sizing: border-box;
   
    transition: .3s cubic-bezier(.215,.61,.355,1);
}

#navService .navInfo {
    position: absolute;
    top: 0;
    left: 47px;
    width: 210px;
    height: auto;
    background: #fff;
    border-bottom-left-radius: 10px;
    -webkit-box-shadow: 0 2px 20px 0 rgba(51,51,51,.2);
    box-shadow: 0 2px 20px 0 rgba(51,51,51,.2);
    z-index: 21;
    padding-top: 15px;
    padding-bottom: 15px;
}
#navService .navHead, #navService .navInfo {
    transition: -webkit-transform .3s cubic-bezier(.215,.61,.355,1);
    transition: transform .3s cubic-bezier(.215,.61,.355,1);
}
#navService .navInfo .swiper-slide {
    padding: 7.5px 12.5px;
    height: auto;
}
#navService .desktop-swiper .swiper-slide {
    width: 50%;
    min-width: 50%;
}
#navService .navInfo a {
    display: block;
    width: 100%;
    text-align: center;
}
#navService .navInfo a .navInfo__icon {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;   
    align-items: center;
    margin: auto;
    border-radius: 50%;
    transition: .3s cubic-bezier(.215,.61,.355,1);
    position: relative;
}
#navService .desktop-swiper {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    width: 100%;
}
#navService .navInfo a .tip {
    letter-spacing: 1.5px;
    line-height: 1.5;
    text-align: center;
  
    transition: color .3s cubic-bezier(.215,.61,.355,1);
}
.rotate {
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}
}





